body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.stats {
  text-align: center;
}
.stats__profile {
  padding: 40px 50px 50px;
  background-color: rgba(111,148,188, 0.1);
  border-radius: 5px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  margin: 3rem;
  margin-right: 0;
}

.stats__profile-container {
    -webkit-justify-content: center;
            justify-content: center;
    display: grid;
    grid-column-gap: 1rem;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.stats__profile-item {
  display: -webkit-flex;
  display: flex;
  text-align: left;
  margin: 1rem;
}

.stats__profile-item:first-child,
.stats__profile-item:nth-child(5) {
  margin-left: 0;
} 

.stats__profile-header {
  margin: 10px 0 20px;
}

.stats__item-name {
  margin: 7px;
}

.stats__repo-button {
  margin: 1rem;
  padding: 10px;
  font-size: 1rem;
  background-color: transparent;
}

.stats__improve {
  font-weight: bold;
}

.stats__link {
  color:#0082C8;
}

.stats__average {
  padding: 2rem;
  margin-top: 20px;
}

.stats__pinnedRepos {
  padding: 40px 50px;
  background-color: rgba(111,148,188, 0.1);
  border-radius: 5px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  height: 500px;
  margin: 3rem;
  margin-right: 0;
}

.col-1-of-3 {
  width: 25%;
  border-right: 1px solid rgba(0,0,0, .1);
}

.col-2-of-3 {
  margin-left: 1rem;
  width: 75%;
}

.row {
  margin: 0 auto;
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  width: 85%;
}

@media only screen and (max-width: 1200px) {
  .row {
    width: 90%;
  }
}
.progressbar {
  padding: 2rem;
  overflow: hidden;
  margin: 0 auto;
  margin-top: 20px;

}

.progressbar__bar {
  margin-bottom: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.progressbar__profile,
.progressbar__repos,
.progressbar__rest {
  height: 10px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.progressbar__profile {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  background-color: #37867E;
}

.progressbar__repos {
  background-color: #99EE86;
}

.progressbar__rest {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: #e2e2e2;
}

.progressbar__labels {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.progressbar__label:first-child {
  position: relative;
  padding-right: 1em;
}

.progressbar__label::before {
  content: '';
  position: absolute;
  width: 14px;
  height: 14px;
  left: 0;
  top: 2px;
  border-radius: 50%;
}

.progressbar__label--profile::before {
  background-color: var(--color-orange);
}

.progressbar__label--repos::before {
  background-color: var(--color-blue);
}

h1 {
  margin: 20px 0;
  font-size: 20px;
}

.progressbar__header-container {
  width: 100%;
  margin: auto;
}
.progressbar__badge {
  width: 80px;
  height: 24px;
  margin: 20px 0 0 0;
}

.tabs-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  position: relative;
  width: 100%;
  margin: 45px 0 0;
  list-style: none;
  background-color: var(--background-color-dark);
}

@-webkit-keyframes bouncy {
  0%{top:0em}
  40%{top:0em}
  43%{top:-0.9em}
  46%{top:0em}
  48%{top:-0.4em}
  50%{top:0em}
  100%{top:0em;}
}

@keyframes bouncy {
  0%{top:0em}
  40%{top:0em}
  43%{top:-0.9em}
  46%{top:0em}
  48%{top:-0.4em}
  50%{top:0em}
  100%{top:0em;}
}

.tabs-list__tab {
  width: 30%;
  display:inline-block;
  padding:0.6em 1.2em;
  margin:0.3em 0.1em 0.3em 0;
  border:0.16em solid rgba(255,255,255,0);
  border-radius:2em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:#FFFFFF;
  text-shadow: 0 0.04em 0.04em rgba(0,0,0,0.35);
  background-color: #0082C8;
  cursor: pointer;
  text-align:center;
  transition: all 0.2s;
  position: relative;
}

.tabs-list__tab:hover {
  background-color: #00A4D6;
  -webkit-animation:bouncy 5s infinite linear;
          animation:bouncy 5s infinite linear;
}
.tabs-list__tab:active {
  background-color:#00937B;
}
.tabs-list__tab--active {
  font-style: italic;
  background-color:#00937B;
}

.tabs-list__content {
  display: none;
  width: 65%;
  min-height: 380px;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  margin: 0;
  background-color:white;
  font-size: 16px;
  overflow-x: hidden;
  overflow-y: auto;
  border-radius: 15px;
}
.tabs-list__tab--active + .tabs-list__content {
  display: block;
}

li {
